import styled, { css } from 'styled-components';
import { mediaQuery } from '../utils/helpers';

interface HideOnProps {
  point: BerakPoints;
}
export const hideOnMobileStyle = css<HideOnProps>`
  display: none;

  ${(p) => css`
    ${mediaQuery(p.point)} {
      display: initial;
    }
  `}
`;

export const HideOn = styled.div<HideOnProps>`
  ${hideOnMobileStyle}
`;
